.header {
  display: flex;
  flex-direction: column;
  row-gap: 3rem;
}

.hero { 
 
  
  &-text__container { 
    
    border: 1px solid red;
    width: 50%;
    height: 20rem;
    // transform: translateY(5rem);
    margin: 0 auto;

  } 
  
  &__heading, &__subheading { 
    animation: .8s fadeUp .8s linear;
  }
  
  // call to action box in the hero section
  &__cta { 
    width: 50%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }
}




// .hero__subheading{ 
//   animation-delay: 1s;
// }



// animations

// fadeUp

@keyframes fadeUp {
  0% { 
    opacity: 0;
    transform:translateY(1rem)
  }

  50% { 
    opacity: .5;
    transform:translateY(-.5rem)

  }

  100%{ 
    opacity: 1;
    transform:translateY(0)

  }
}

